.in_Holder{
    margin: 20px 10px;
    display: flex;
    flex-direction: row;
    font-family: 'Poppins','Times New Roman', Times, serif;
    justify-content: space-evenly;
}
.in_container{
    background-color: #ededeb;
    height: 220px;
    border-radius: 10px;
    width: 350px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

.icon_Art_Das , .icon_Art_Das2 ,.icon_Art_Das3{
    font-size: 40px;
    margin-right: 10px;
    border-radius: 50%;
    background-color: #ade64c;
    padding: 22px;

}
.seter{
    display: flex;
    align-items:center;
    font-size: 35px;
    
    color: black;
    background: transparent;
   
    font-family: 'poppins';
    font-weight: 600;
    margin-top: 0px;
    margin-left: 20px;
    
}
.details_art{
    text-align: right;
}
.icon_Art_Das2{
    background-color: #f08c35;
}
.icon_Art_Das3{
    background-color: #2799db;
}
.total_art{
    
    background-color: #8f8f8f;
}
.Top_Artistifo{
    background-color: #d1d1d1;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
    height: 100px;
    font-size: 20px;
    font-family: 'poppins';
    font-weight: 500;
    border-radius: 30px 30px 0px 0px;
}
.in_container:hover{
    box-shadow: -3px 3px 10px  #8f8f8f;
}
#todaysI{
    font-size: 14px;
    margin-right: 15px;
    font-weight: 600;
}
.component{
    display: flex;
    flex-direction: row;
    width: 100%;
}
.post_com_artist{
    width: 67.7%;
}
.friends_com_artist{
    width: 340px;
    padding-bottom: 15px;
    border-radius: 10px;
    background-color: #ededeb;
    height: fit-content;
   
}
.friends_hold{
    width: 90%;
    margin-top: 5px;
    display: flex;
   align-items: center;
    font-size: 17px;
    border:1px solid black;
    padding: 5px 0;
    margin-left: 5%;
    border-radius: 5px;
    font-weight: 600;
}
.friends_hold:nth-of-type(1){
    margin-top: 20px;
}

.sat{
    margin-left: 10px;
    width: 37px;
    height: 37px;
    border-radius: 50%;
}
#id{
    margin-top: -5px;
    margin-left: 10px;
}
.YourBandDiv{
    display: flex;
    flex-direction: column;
    background-color: #ededeb;
    width: 340px;
    overflow: hidden;
    height: 180px;
    border-radius: 10px;
    font-family: 'poppins';
}
#bandLogo{
    width: 80px;
    margin-top: 10px;
    margin-left: 10px;
    height: 80px;
    border-radius: 50%;
    margin-right: 10px;
}
#PIC_LINE_CONT{
    display: flex;
    font-weight: 600;
    align-items: center;
}
#Poper_A{
    display: flex;
    flex-direction: column;
    align-items: start;
}
.BandName2{
    font-size: 14px;
}
.more_artist_btn{
    margin-left: 285px;
    margin-top: 0px;
    border-radius: 10px 0 0 0;
    cursor: pointer;
    padding: 6px 5px 5px 9px;
    color: white;
    font-size: 14px;
    background-color: #9045d1;
}