@import url("https://fonts.googleapis.com/css2?family=Fira+Sans+Condensed:ital,wght@1,600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lobster+Two&display=swap");

.Nav-Con {
    background-color: #ededeb;
    height: 100vh;
    margin-left: 0px;

}

.holder {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.profile-holder {
    margin-top: 30px;
    text-align: center;
    width: 100%;
    justify-content: center;

    align-items: center;
}

ul {
    list-style-type: none;
    padding: 0;
    margin-top: 5px;
}

li {
    font-family: 'Poppins', sans-serif;
    display: flex;
    padding: 8px;
    transition: all 0.5s;
    font-size: 16.2px;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    align-items: center;
    margin-bottom: 5px;
    cursor: pointer;

}

li:hover {
    background: rgb(237, 237, 235);
    background: linear-gradient(90deg,
            rgba(237, 237, 235, 1) 0%,
            rgba(144, 69, 209, 1) 36%);
    color: white;
}

.B_icon {
    font-size: 20px;
    margin-left: -20px;
    font-weight: 500;
    margin-left: 40px;
    margin-right: 10px;
}

.username {
    color: #9045d1;


    font-weight: 500;

    text-shadow: 2px 2px 20px #9045d1;
    font-size: 40px;
    font-family: "Lobster Two", cursive;
}

.tag1 {
    margin-top: 50px;
    font-family: 'Poppins', sans-serif;
    margin-bottom: 10px;
    margin-left: 10px;
    color: #8f8f8f;
    font-size: 17px;
    display: flex;
    align-items: center;

}

.tag {
    margin-top: 15px;
    font-family: 'Poppins', sans-serif;
    margin-bottom: 10px;
    margin-left: 10px;
    color: #8f8f8f;
    font-size: 17px;
    display: flex;
    align-items: center;
}