.Apayment{
  display: flex;
  flex-direction: row;
 
 }
 .nav{
     width: 17%;
   
 }
 .containt{
     width: 82%;
     overflow-y: scroll;
     overflow-x: hidden;
     height: 100vh;
 }
.todayPayment{
    width: 90%;
    height: 173px;
    margin-left: 50px;
    background-color: #ededeb;
    margin-top: 10px;
    border-radius: 10px;
    padding: 0px 10px 10px 10px;
    
   }
   .mt{
    padding-top: 10px;
   }
   .this{
    font-size: 35px;
    font-weight: 600;
    color: #8f8f8f;
    margin-top: 10px;
    border-radius: 10px;
    border: 2px solid #8f8f8f;
    padding: 0 30px 15px 30px;
   }
   #thisweek{
    color: black;
    font-size: 15px;
    font-weight: 700;
    font-family: 'poppins';
   }
   .hidden{
    display: flex;
    align-items: baseline;
    justify-content: space-evenly;
   }
   .table{
    width: 91%;
    border-radius: 10px;
    margin-top: 10px;
    height: 320px;
    background-color: #ededeb;
    margin-left: 50px;
    overflow-x: hidden;
    overflow-y: scroll;
   }
   table{
    width: 100%;
    font-family: 'poppins';
    font-size: 15px;
    text-align: center;
   }
   tr:nth-child(even) {
    background-color: #dddddd;
  }
   td{
    width: 400px;
    padding: 10px;
    
   }
   th{
    width: 400px;
    padding: 10px;
   }