.dash_Holder {
    margin-top: 15px;
    margin-left: 10px;
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.Search {
    outline: none;
    width: 200px;
    font-family: 'Poppins', sans-serif;
    border-radius: 10px;
    padding: 5px;
    border: 1px solid black;
    background-color: white;
}

.Noti {
    font-size: 27px;
    cursor: pointer;
    color: #8f8f8f;
    margin-right: 7px;
}

.Chat {
    font-size: 27px;
    cursor: pointer;
    color: #8f8f8f;
    margin-right: 15px;
}

.profile_pic {
    width: 35px;
    height: 35px;

    transition: all 0.5s;
    border-radius: 50%;
    margin-right: 15px;
    cursor: pointer;
}

.profile_pic:hover {
    box-shadow: 0 0 10px 2px hsl(272, 60%, 55%);
}

.post {
    justify-content: space-evenly;
    display: flex;
    margin: 20px 0;
}

.PostContainer {
    width: 58%;
    height: 83vh;
    overflow-y: scroll  ;
    padding: 0px 0px;
    border-radius: 10px;
    background-color: none;
}

.R_PostContainer {
    width: 38%;
    height: 83vh;
    overflow-y: scroll;
    padding: 10px 0;
    border-radius: 10px;
    background-color: #ededeb;
}

.createPost {
    width: 100%;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    background-color: #ededeb;
}

.C_profile {
    width: 37px;
    height: 37px;
    transition: all 0.5s;
    border-radius: 50%;
    margin-right: 10px;
    cursor: pointer;
    margin-left: 10px;
}

.P_User_holder {
    display: flex;
    font-family: 'poppins';
    font-weight: 600;
    margin: 10px;
    align-items: center;
}

.UerIdea {
    font-family: 'poppins';
    width: 75%;
    border: none;
    color: black;
    margin-top: 10px;
    margin-left: 70px;
    border-bottom: 1px solid black;
    border-radius: 2px;
    outline: none;
    background-color: transparent;
}

.Upload_pic {
    font-size: 25px;
    cursor: pointer;
    padding: 4px;
    color: white;
    margin: 0;
    border-radius: 5px;
    background-color: #9045d1;
}

.Upload_pic:hover {
    box-shadow: 0 0 5px #9045d1;
}

.input_img {
    margin-left: 70px;
    display: flex;
    width: 75%;
    margin-bottom: 10px;
    color: transparent;
    justify-content: space-between;
    align-items: center;
}

.Anounsment {
    font-size: 17px;
    cursor: pointer;
    padding: 4px;
    margin: 0;
    color: white;
    border-radius: 5px;
    background-color: #9045d1;
}

.Button_Pic {
    margin-top: 10px;
    background-color: transparent;
    outline: none;
    border: none;
}

.Upload_post {
    font-size: 15px;
    font-family: 'poppins';
    background-color: #9045d1;
    outline: none;
    border: none;
    padding: 5px 7px;
    display: flex;
    width: 80px;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 5px;
    border-radius: 5px;
    color: white;
}

.Upload_post:hover {
    box-shadow: 0 0 10px #9045d1;
    cursor: pointer;
}

