.Oders{
    display: flex;
    flex-direction: row;
   
   }
   .nav{
       width: 17%;
     
   }
   .containt{
       width: 82%;
       overflow-y: scroll;
       overflow-x: hidden;
       height: 100vh;
   }
   .Oders_holder{
    display: flex;
    flex-direction: row;
    margin-top: 10px;
   justify-content: space-evenly;
   
   }
   .Oders_con{
    background-color: #ededeb;
    width: 300px;
    height: 80px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    font-size: 17px;
   }
   #center{
    display: flex;
    margin-left: 10px;
    font-family: 'poppins';
    flex-direction: column;
   }
   .icon_oders{
    background-color: #9045d1;
    padding: 15px;
    border-radius: 50%;
    margin-left: 10px;
    color: white;
    font-size: 28px;
    
   }
   .table1{
    width: 91%;
    border-radius: 10px;
    margin-top: 10px;
    height: fit-content;
    background-color: #ededeb;
    margin-left: 50px;
    overflow-x: hidden;
    overflow-y: scroll;
   }
   table{
    width: 100%;
    font-family: 'poppins';
    font-size: 15px;
    text-align: center;
   }
   tr:nth-child(even) {
    background-color: #dddddd;
  }
   td{
    width: 400px;
    padding: 10px;
    
   }
   th{
    width: 400px;
    padding: 10px;
   }