
.dash_Holder{
    margin-top: 15px;
    margin-left: 10px;
    display: flex;
    width: 100%;
    justify-content: space-between;
}
.Search{
    outline: none;
    width: 200px;
    font-family: 'Poppins', sans-serif;
    border-radius: 5px;
    padding: 5px;
    border: 1px solid black;
    background-color: white;
}

.Noti{
    font-size: 27px; 
    cursor: pointer;
    color: #8f8f8f;
    margin-right: 7px;
}
.Chat{
    font-size: 27px; 
    cursor: pointer;
    color: #8f8f8f;
    margin-right: 15px;
}
.profile_pic{
    width: 35px;
    height: 35px;
    transition: all 0.5s;
    border-radius: 50%;
    margin-right: 10px;
    cursor: pointer;
}
.profile_pic:hover{
    box-shadow: 0 0 10px  2px hsl(272, 60%, 55%);
}
.A-Tager{
    margin-top: 20px;
    font-size: 25px;
    font-family: 'poppins';
    font-weight: 600;
    display: flex;
    align-items: center;
    color: #8f8f8f;
    margin-left: 5px;
}
.Top-Holder{
    display: flex;
    flex-direction: row;
    width: 97%;
    margin-left: 25px;
    margin-top: 10px;
    justify-content: space-evenly;
}
.top{
    display: flex;
    background-color: #ededeb;
    width: 200px;
    height: 250px;
    position: relative;
    font-family: 'poppins';
    border-radius: 10px;
    justify-content: center;
    align-items: center;   
    flex-direction: column;
}
.A-pic{
    width: 60px;
    height: 60px;
    border-radius: 100%;
    box-shadow: 0px 0px 10px 1px #9045d1;
}
.A-name{
    margin-top: 10px;
    font-size: 19px;
    font-weight: 600;
}
.A-loaction{
    font-size: 12px;
    display: flex;  
    align-items: center;
}
.A-rating{
    font-size: 15px;
    margin-top: 10px;
    display: flex;
    align-items: center;
}
.A_B_potion{
    margin-bottom: -20px;
}
.Sch{
    position: absolute;
    margin-top: -200px;
    font-size: 30px;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    margin-left: -150px;
}
.A-regular-A{
    width: 94.5%;
    margin-top: 10px;
    border-radius: 10px;
    margin-left: 40px;
    display: flex;
    height: 400px;
    margin-bottom: 10px;
    flex-direction: column;
    padding-bottom: 10px;
    background-color: #ededeb;
}
.REG_nav{
    margin-left: 10px;
    margin-top: 10px;
    display: flex;
    height: 40px;
    align-items: center;
}
.REG_button{
    font-size: 25px;
    background-color: #9045d1;
    padding: 4px;
    margin-left: 10px;
   cursor: pointer;
   transition: all 0.3s;
   color: white;
   margin-right: -5px;
    border-radius: 5px;
}

.REG_button:hover{
    box-shadow: 0 0 7px 1px #9045d1;
}
.REG_holder{
    margin-top: 5px;
    margin-left: 20px;
    display: flex;
    width: 95%;
    height: 50px;
    border-radius: 7px;
    background-color: #d4d4d2;
    align-items: center;
}
.R_A_NAME{
    font-size: 17px;
    margin-left: 5px;
    width: 100%;
    display: flex;
    font-weight: 600;
    justify-content: space-evenly;
    font-family: 'poppins'
}
.R_A_IMG{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-left: 20px;
}
.REG_Container{
    height: auto;
    overflow-y: scroll;
    overflow-x: hidden;
    margin-top: 10px;
}
.maintain_b{
    background-color: #9045d1;
    border: none;
    color: white;
    font-size: 13px;
    font-family: 'poppins';
    outline: none;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 5px;
    margin-top: 0px;
    padding: 8px 10px;
}
.maintain_b:hover{
    box-shadow: 0 0 7px 2px #9045d1;
}
.clutch{
    display: flex;
    align-items: center;
}
.aligner{
    display: flex;
    align-items: center;
}
.set{
    margin-top: 4px;
}