.dash_Holder{
    margin-top: 15px;
    margin-left: 10px;
    display: flex;
    width: 100%;
    justify-content: space-between;
}
.Search{
    outline: none;
    width: 200px;
    font-family: 'Poppins', sans-serif;
    border-radius: 5px;
    padding: 5px;
    border: 1px solid black;
    background-color: white;
}

.Noti{
    font-size: 27px; 
    cursor: pointer;
    color: #8f8f8f;
    margin-right: 7px;
}
.Chat{
    font-size: 27px; 
    cursor: pointer;
    color: #8f8f8f;
    margin-right: 15px;
}
.profile_pic{
    width: 35px;
    height: 35px;
    transition: all 0.5s;
    border-radius: 50%;
    margin-right: 10px;
    cursor: pointer;
}
.profile_pic:hover{
    box-shadow: 0 0 10px  2px hsl(272, 60%, 55%);
}