.transperant {
    background: rgba(0, 0, 0, 0.5);
    backdrop-filter: saturate(110%) blur(30px);
    border-radius: 10px;
    width: 500px;
    height: 300px;
    color: white;
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;

}

#LoginForm , #LoginForm1{
    border: 1px solid white;
    border-radius: 10px;
    outline: none;
    padding-left: 15px;
    margin: 10px 10px 0px 10px;
    width: 200px;
    background-color: transparent;
    height: 30px;
    color: white;
}

.LoginBtn{
    background-color: lightgreen;
    border: none;
    outline: none;
    margin-top: 30px;
    width: 50px;
    font-size: 23px;
    padding: 5px 0 0 0;
    border-radius: 50%;
    height: 50px;
    cursor: pointer;
    overflow-x: hidden;

    transition: all 0.4s;
}

.LoginBtn:hover {
    box-shadow: 0 0 10px 2px lightgreen;

}

.L_title {
    font-size: 30px;
    margin-bottom: 20px;
}